<template>
  <div>
    <v-row>
      <v-col
        cols="12"
        sm="6"
        md="4"
        v-for="(assignment, index) in allAssignments"
        :key="index"
        @click="viewAssignment(assignment)"
        :disabled="assignment.answers == null"
      >
        <v-card dark color="primary" shaped>
          <v-card-title>
            <v-row>
              <v-col cols="1">
                <v-icon>mdi-school</v-icon>
              </v-col>
              <v-col cols="10">{{ assignment.student }}</v-col>
            </v-row>
          </v-card-title>
          <v-card-text> Mark: {{ assignment.mark }} </v-card-text>
        </v-card>
      </v-col>

      <v-snackbar v-model="notAnsweredDialog" color="error">
        <h2>This student didn't answer that homework!</h2>
      </v-snackbar>
    </v-row>
  </div>
</template>

<script>
import HomeworkService from "../services/HomeworkService";
import StudentService from "../services/StudentService";
export default {
  data() {
    return {
      homework: {},
      allAssignments: [],
      homeworkId: Number,
      notAnsweredDialog: false
    };
  },
  created() {
    this.homeworkId = this.$route.params.homeworkId;
    HomeworkService.GetHomework(this.homeworkId).then(homework => {
      this.homework = homework;
    });

    HomeworkService.GetAssignments(this.homeworkId).then(allAssignments => {
      for (let i = 0; i < allAssignments.length; i++) {
        const el = allAssignments[i];

        if (el.studentId == null) {
          continue;
        }

        this.allAssignments.push(el);

        StudentService.GetStudent(el.studentId).then(student => {
          el.student = student.name;
          this.allAssignments.splice(i, 1, el);
        });
      }
    });
  },
  methods: {
    viewAssignment(assignment) {
      if (assignment.answers != null) {
        this.$router.push({
          name: "ViewAssignment",
          params: {
            assignmentId: assignment.id,
            homeworkId: this.homeworkId,
            studentId: assignment.studentId
          }
        });
      } else {
        this.notAnsweredDialog = true;
      }
    }
  }
};
</script>

