import axios from 'axios'

const url = '/homeworks/'

class HomeworkService {
    static async GiveHomework(homeworkBody) {
        const response = await axios.post(url, homeworkBody)
        return response.data
    }

    static async GetAssignments(homeworkId) {
        const response = await axios.get(url + homeworkId + '/assignments')
        return response.data
    }

    static async GetHomework(homeworkId) {
        const response = await axios.get(url + homeworkId)
        return response.data
    }

    static async DeleteHomework(homeworkId) {
        const response = await axios.delete(url + homeworkId)
        return response.data
    }

    static async ReviewAssignment(assignment) {
        const response = await axios.patch('/assignments/review/' + assignment.id, assignment)
        return response.data
    }

    static async GetAssignment(assignmentId) {
        const response = await axios.get(url + '/assignments/' + assignmentId)
        return response.data
    }

    static async GetHomeworkCourse(homeworkId) {
        const response = await axios.get(url + homeworkId + '/course')
        return response.data
    }
}

export default HomeworkService;